/* global d3 */
import enterView from 'enter-view';
function resize() { }

function init() {
  console.log('Make something awesome!');

  enterView({
    selector: '.chapter',
    enter: function (el) {
      const id = d3.select(el).attr('id');
      console.log('chapter-id', id);
      const allImg = d3.select(el).selectAll('.img').nodes()
      allImg.forEach(img=>{
        console.log( img.dataset.src );
        img.src = img.dataset.src;
      });
    },
    offset: 0, // enter at middle of viewport
  });

  enterView({
    selector: '.chapter_hed_img_ctn',
    enter: function (el) {
      d3.selectAll('.chapter_hed_img_ctn').classed('active', false);
      el.classList.add('active');
    },
    offset: 0.1, // enter at middle of viewport
  });

}

export default { init, resize };
